import React, { useState } from 'react';
import './App.scss';
import "aos/dist/aos.css";
import menu from './assets/img/burgermenu.svg';
import Main from './Main';
import Works from './works';
import Aboutme from './Aboutme';
import { HashRouter,BrowserRouter, NavLink, Redirect, Route } from 'react-router-dom';
import Cases from './Cases';
import instagramIcon from './assets/img/instIcon.svg';
import whatsappIcon from './assets/img/whatsappIicon.png';

function App() {

  const [menuToggler, setMenuToggler] = useState(false);

  let [menuStyles, setmenuStyles] =useState(1);

  let [zxc, setzxc] = useState(false);

  const windowWidth = window.innerWidth;

  const CloseMenu = () => {
    if (windowWidth<=940) {
      setMenuToggler(false);
    }
  }
  return (
    <HashRouter basename={process.env.PUBLIC_URL}>
    {!zxc && <Redirect to={'/main'}/>}
    <div className="App" id='App'>
        <div className="menu image">
          <img src={menu}  onClick={()=> {setMenuToggler(!menuToggler)}}/>
        </div>
        {menuToggler&&<div className={`menu-block menu-block-${menuStyles}`}>
          <div className="menu-wrapper">
              <NavLink to="/main" className={`link link-${menuStyles}`} onClick={CloseMenu}>

                  ГЛАВНАЯ

                </NavLink>
              <NavLink to="/aboutme" className={`link link-${menuStyles}`} onClick={CloseMenu}>

                  ОБО МНЕ

              </NavLink>
              <NavLink to="/works" className={`link link-${menuStyles}`} onClick={CloseMenu}>

                  РАБОТЫ

              </NavLink>
            
              <NavLink to="/services" className={`link link-${menuStyles}`} onClick={CloseMenu}>
                УСЛУГИ
              </NavLink>
              {(windowWidth<=940)? 
              <div className="Links">
              <a href='https://www.instagram.com/kirill.who/'><img src={instagramIcon}/></a>
              <a href='https://wa.me/+393661571994'><img src={whatsappIcon}/></a>
              </div>
               : null}
          </div>
        </div>}
        <Route path='/main' render={() => <Main setMenuToggler={setMenuToggler} setmenuStyles={setmenuStyles}/>}/>
        <Route path='/aboutme' render={() => <Aboutme setMenuToggler={setMenuToggler} setmenuStyles={setmenuStyles}/>}/>
        <Route path='/services'  render={() => <Cases setMenuToggler={setMenuToggler} setmenuStyles={setmenuStyles}/>}/>
        <Route path='/works' render={() => <Works setMenuToggler={setMenuToggler} setmenuStyles={setmenuStyles}/>}/>
    </div>
    </HashRouter>
  );
}

export default App;
