import React, { useEffect } from 'react';
import './works.scss';
import work1 from './assets/img/work1.png';
import work2 from './assets/img/work2.png';
import work3 from './assets/img/work3.png';
import work4 from './assets/img/work4.png';
import work5 from './assets/img/work5.png';
import work6 from './assets/img/work6.png';
import work7 from './assets/img/work7.png';
import work8 from './assets/img/work8.png';
import work9 from './assets/img/work9.png';
import work10 from './assets/img/work10.png';


function Works(props) {

  useEffect ( () => {
    props.setmenuStyles(1);
  });

  return (
    <div className="Works" onClick={() => {props.setMenuToggler(false)}}>
      <div className="work work-10 image"> 
        <img src={work10}/>
      </div>
      <div className="work work-9 image"> 
        <img src={work9}/>
      </div> 
      <div className="work work-7 image"> 
        <img src={work7}/>
      </div>
      <div className="work work-2 image"> 
        <img src={work2}/>
      </div>
      <div className="work work-3 image"> 
        <img src={work3}/>
      </div>
      <div className="work work-4 image"> 
        <img src={work4}/>
      </div>
      <div className="work work-5 image"> 
        <img src={work5}/>
      </div>
      <div className="work work-6 image"> 
        <img src={work6}/>
      </div>
      
      
      <div className="work work-8 image"> 
        <img src={work8}/>
      </div>
      
      <div className="work work-1 image"> 
        <img src={work1}/>
      </div>
      
    </div>
  );
}

export default Works;